<template>
  <view class="activity-result-container">
    <image :src="success_icon" class="img" mode=""></image>
    <view class="title">{{ title }}</view>

    <view class="login-btn">
      <uv-button type="info" shape="circle" loadingText="登录中" ripple @click="jumpToQrcode">
        返回首页
      </uv-button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      title: '报名成功',
    }
  },
  computed: {
    success_icon() {
      return this.$store.app.currentThemeIconByType['SUCCESS_ICON']
    },
  },
  onLoad(options) {
    if (options.title) {
      this.title = options.title
    }
    if (this.type == 'train') {
      uni.setNavigationBarTitle({ title: '报名结果' })
    }
    if (this.type == 'activity') {
      uni.setNavigationBarTitle({ title: '发布结果' })
    }
  },
  methods: {
    jumpToQrcode() {
      uni.switchTab({ url: '/pages/tabbar/home/index' })
    },
  },
}
</script>

